.Menu{
    min-height: 80vh;
    height: 100%;
    justify-content: space-around;
    width: 100vw;
    pointer-events: none;
    opacity: 1;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.Menu.disable{
    opacity: 0;
    display: none;
}

.Text{
    text-align: center;
    font-size: 2vw;
}