.Info{
    display: flex;
    flex-direction: column;
    margin-left: 3.7vw;
}

.Cyrcles{
    display: flex;
    align-items: center;
    width: 20.6777vw;
    justify-content: space-between;
}

.Cyrcle{
    margin-top: 0.5vw;
    width: 0.33vw;
    height: 0.33vw;
    border-radius: 26.04vw;
}
.ContainerCyr{
    display: flex;
    width: 3.02vw;
    justify-content: space-between;
}
.Cyrcles p{
    margin: 0;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 1.67vw;
    line-height: 1.3vw;
}

.Cyrcle:nth-child(1){
    border: 0.078vw solid #12C868;
}

.Cyrcle:nth-child(2){
    border: 0.078vw solid rgba(18, 200, 104, 0.75);
}

.Cyrcle:nth-child(3){
    border: 0.078vw solid rgba(18, 200, 104, 0.5);
}

.Cyrcle:nth-child(4){
    border: 0.078vw solid rgba(18, 200, 104, 0.25);
}

.end{
    margin: 0;
    margin-top: 1.98vw;
    width: 12.4vw;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 0.73vw;
    line-height: 1.1458vw;
    text-align: center;
    letter-spacing: 0.02em;
    color: #1C1C1C;
}

.end span{
    font-weight: 500;
}

@media screen and (max-width: 980px){
    .Info{
        align-items: center;
        margin-left: 0;
    }
    .Cyrcle:nth-child(1){
        border: 0.278vw solid #12C868;
    }

    .Cyrcle:nth-child(2){
        border: 0.278vw solid rgba(18, 200, 104, 0.75);
    }

    .Cyrcle:nth-child(3){
        border: 0.278vw solid rgba(18, 200, 104, 0.5);
    }

    .Cyrcle:nth-child(4){
        border: 0.278vw solid rgba(18, 200, 104, 0.25);
    }
    .Cyrcle{
        width: 1.1vw;
        height: 1.1vw;
    }
    .Cyrcles{
        width: 53vw;
    }
    .Cyrcles p{
        font-size: 3.61vw;
        line-height: 4.44vw;
    }
    .ContainerCyr{
        width: 12vw;
    }
    .BTNTEXT{
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
    }
    .end{
        width: 50vw;
    }
    .end p{
        font-size: 2.22vw;
        line-height: 3.33vw;
    }
}
