.WorkTime{
    justify-content: center;
    display: flex;
    margin: 0;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 0.625vw;
    line-height: 0.9vw;
    text-align: center;
}

.WorkTime p{
    margin: 0;
}

.WorkTime span{
    font-weight: 700;
}

.Number{
    justify-content: center;
    display: flex;
    margin-bottom: 0.3vw;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 1.146vw;
    margin-top: 0.31vw;
    line-height: 1.7vw;
    text-align: center;
    letter-spacing: 0.05em;
}

.Number p{
    margin: 0;
}

.CallMe{
    cursor: pointer;
    display: flex;
    justify-content: center;
}

.CallMe p{
    margin: 0;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 0.625vw;
    line-height: 0.78vw;
    text-align: center;
    letter-spacing: 0.04em;
    color: #12C868;
    border-bottom: #12C868 dotted 0.2vw;
}

@media screen and (max-width: 980px){
    .WorkTime{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 1.94vw;
        line-height: 2.5vw;
    }
    .WorkTime span{
        font-weight: 700;
    }
    .Number{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 3.9vw;
        line-height: 4.72vw;
    }
    .CallMe p{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 2.778vw;
        line-height: 3.33vw;
        text-align: center;
    }
}