.Bullets{
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url("../../../../png/Форма (2) (1).png");
    background-size:cover;
    background-repeat: no-repeat;
    height: 32.631vw;
    width: 11.72vw;
    box-shadow: 0px 0px 3.9vw rgba(0, 0, 0, 0.03);
    transition: 0.5s;
    border-radius: 20px;

}


.Main{
    width: 70%;
    height: 90%;
    justify-content: space-evenly;
    display: flex;
    flex-direction: column;
}

.IMGDIV{
    max-height: 1vw;
}

.Main img{
    display: block;
    width: 100%;
}

.Main p{
    margin: 0;
    align-items: center;
    text-align: center;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 0.73vw;
    line-height: 1.146vw;
}

.Main p span{
    font-weight: 700;
}

.Shadow:nth-child(5){
    border-bottom: 0;
}

.Main p:hover{
    cursor: pointer;
    text-decoration: underline;
}

.Close{
    display: none;
}


@media screen and (max-width: 980px){
    .Close{
        display: flex;
    }
    .Bullets{
        display: none;
        width: 60vw;
        height: 60vh;
        position: sticky;
        bottom: 0;
        justify-content: space-around;
    }

    .Main p{
        font-size: 1.5vw;
        line-height: normal;
    }
