.Nav{
    align-items: center;
    display: flex;
    justify-content: space-between;
    border-bottom: 0.049vw solid rgba(0, 0, 0, .07);
}

.Nav svg{
    height: 1.146vw;
}

.Nav p{
    margin-top: 2.1923vw;
    margin-bottom: 1.8934vw;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 0.76vw;
    line-height: 140%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #1C1C1C;

}

.Nav p:hover{
    cursor: pointer;
    color: rgba(255, 0, 0, .6);
}

@media screen and (max-width: 980px){
    .Nav{
        height: 45vw;
        flex-direction: column;
        border: none;
    }
    .Nav svg{
        height: 6.38vw;
    }
    .Nav p{
        margin: 0;
        font-size: 2vw;
    }
}