.App-wrapper{
    background: url("./png/BG.jpg");
    background-size:cover;
    min-height: 100vh;
    height: 100%;
    width: 100vw;
}

.CONT{
    height: 100%;
    width: 81.8%;
    margin-right: auto;
    margin-left: auto;
}


.outModal svg{
    display: block;
    width: 1.04vw;
    max-height: 1.04vw;
}

.outModal{
    cursor: pointer;
    display: flex;
    justify-content: flex-end;
}
.MainInfoModal p{
    margin: 0;
    text-align: center;
}

.FirstP{
    margin-top: 0.26vw;
    margin-bottom: 0.73vw;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 1.6vw;
    line-height: 1.52vw;
    letter-spacing: 0.02em;
}

.FirstP p:nth-child(1){
    margin-bottom: 0.52vw;
}

.SecondP{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 0.83vw;
    line-height: 1.25vw;
}
.ThirdP{
    padding-top: 0.833vw;
    padding-bottom: 1.458vw;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 0.625vw;
    line-height: 0.938vw;
}

.MainInfoModal{
    margin-right: auto;
    margin-left: auto;
    width: 18.542vw;
    display: flex;
    flex-direction: column;
}

.Input{
    margin-bottom: 1.4583vw;
}

.InputText{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 0.625vw;
    line-height: 0.78vw;
    padding-left: 2.4vw;
    display: flex;
}

.Input input{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 0.9375vw;
    line-height: 1.04vw;
    margin-top: 0.625vw;
    box-sizing: border-box;
    padding-left: 2.4vw;
    width: 100%;
    height: 4.43vw;
    border-radius: 26vw;
    border: none;
}

button{
    width: 100%;
    background: #F38120;
    box-shadow: inset 0px 0px 0.885vw rgba(255, 255, 255, 0.25);
    border-radius: 26vw;
    border: none;
}

.Checkbox{
    align-items: center;
    margin-bottom: 1.1vw;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 0.625vw;
    line-height: 1.1vw;
    margin-top: 1vw;
    display: flex;
}

.Checkbox input{
    border: #f38120 solid 0.052vw;
    width: 1.1vw;
    transform: scale(1.5);
    background-color: #F38120;
    display: flex;
    margin: 0;
}

.Checkbox p{
    padding-left: 0.99vw;
    text-align: left;
}

.Checkbox p span{
    color: #F38120;
}

.ButtonFormText{
    color: #FFFFFF;
    justify-content: space-around;
    margin-left: auto;
    margin-right: auto;
    width: 60%;
    height: 4.43vw;
    display: flex;
    align-items: center;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 0.833vw;
    line-height: 1.0416vw;
}

.ButtonFormText svg{
    width: 1.51vw;
}

@media screen and (max-width: 980px){
    .FormContainer{
        transform: scale(2);
    }
    .CONT{
        width: 100vw;
    }
    .App-wrapper{
        box-sizing: border-box;
        min-height: 100vh;
        background-position-x: center;
        background-position-y: center;
    }
    .Header{
        display: none;
    }
}