.modal{
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.6s;
    opacity: 0;
    pointer-events: none;
}

.modal.active{
    pointer-events: all;
    height: 100vh;
    width: 100vw;
    opacity: 1;
}

.ModalContent{
    padding: 2.344vw;
    border-radius: 1.822vw;
    width: 23vw;
    background: rgba(245, 249, 249);
    opacity: 0;
    transform: scale(0);
    transition: 0.6s;
}

.ModalContent.active{
    transform: scale(1);
    opacity: 1;
}

@media screen and (max-width: 980px){
    .ModalContent{
        height: 55vw;
        width: 40vw;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}