.cyrcle{
    width: 0.21vw;
    height: 0.21vw;
    background: #12C868;
    border-radius: 100px;
}

.Messanders{
    width: 8.32vw;
}

.Text{
    display: flex;
    align-items: center;
    justify-content: center;
}

.Text p{
    margin: 0 0 0 0.21vw;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 0.64vw;
    text-align: center;
    letter-spacing: 0.02em;
    color: #1C1C1C;
}

.Text p span{
    font-weight: 700;
}

.MS{
    margin: 0;
    display: flex;
    margin-top: 0.85vw;
    justify-content: space-between;
}

.MS svg{
    width: 2.28vw;
}

@media screen and (max-width: 980px){
    .Text p{
        font-size: 1.6vw;
    }
    .MS{
        margin-top: 15px;
    }
    .cyrcle{
        width: 0.7vw;
        height: 0.7vw;
    }
    .MS svg{
        width: 10vw;
    }
    .Messanders{
        width: 40vw;
    }
}