.MainText p{
    font-family: 'Montserrat';
    font-weight: revert;
    font-size: 2.85vw;
    line-height: 4.6vw;
    text-align: center;
    color: black;
    width: 33.8vw;
    margin: 0;
    margin-top: 1.3vw;
    margin-bottom: 1vw;
}

.MainText p span{
    font-weight: 600;
}

@media screen and (max-width: 980px){
    .MainText p{
        margin-right: auto;
        margin-left: auto;
        font-size: 5vw;
        width: 80%;
        line-height: 8.33vw;
    }
}
