.Buttons{
    margin-top: 2.344vw;
    display: flex;
    justify-content: space-between;
    width: 40vw;
}

.Button:nth-child(1){
    background: #12C868;
    width: 19.53vw
}

.Button:nth-child(2){
    background: #F38120;
    width: 17.48vw
}

.Button{
    cursor: pointer;
    box-shadow: inset 0px 0px 1.145vw rgba(255, 255, 255, 0.25);
    border-radius: 26vw;
    border: none;
    height: 5.2vw;
}

.Button:nth-child(1).active{
    background: #21E27C;
}

.Button:nth-child(2).active{
    background: #FD9C49;
}

.Container.active{
    width: 13vw;
}

.Container p{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 0.73vw;
        line-height: 1.146vw;
        text-transform: uppercase;
        width: 11.094vw;
        color: #FFFFFF;
        text-align: center;
}

.Container{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 13.75vw;
    transition: 0.5s;
}

.Button{
    display: flex;
    justify-content: center;
}

.Button svg{
    height: 1.04167vw;
    width: 1.04167vw;
}

@media screen and (max-width: 980px){
    .Button:nth-child(1){
        margin-bottom: 20px;
        width: 76.66vw;
        height: 16.66vw;
    }
    .Button:nth-child(2){
        width: 76.66vw;
        height: 16.66vw;
    }
    .Buttons{
        flex-direction: column;
        align-items: center;
    }
    .Container{
        width: 80%;
    }
    .Container.active{
        width: 75%;
    }
    .Container p{
        width: 80%;
        font-size: 2.78vw;
        line-height: 4.44vw;
    }
    .Container svg{
        width: 4.16vw;
        height: 4.16vw;
    }
}


@media screen and (max-width: 980px){
    .Buttons{
        margin-bottom: 10vw;
    }
}


