.Dowload{
    display: flex;
    align-items: center;
}

.PDF{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-content: center;
    width: 2.28vw;
    height: 2.28vw;
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.05);
    border-radius: 30vw;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 0.6vw;
    text-align: center;
    letter-spacing: 0.04em;
    color: #12C868;
}

.PDF:hover{
    color: rgba(255, 0, 0, .6);;
}

.Text{
    margin-left: 0.89vw;
}

.Text p{
    cursor: pointer;
    display: flex;
    margin: 0;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 0.64vw;
    text-align: center;
    letter-spacing: 0.04em;
    color: #1C1C1C;
    border-bottom: #12C868 dotted 0.2vw;
}

.PDF p{
    margin: 0;
    justify-content: center;
    align-items: center;
    display: flex;
}


@media screen and (max-width: 980px){
    .PDF{
        width: 6vw;
        height: 6vw;
    }
    .PDF p{
        font-size: 1.3vw;
    }
    .Text p{
        font-size: 2.5vw;
    }
}