.TopInfo{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.Text{
    text-align: center;
    width: 10.8vw;
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 0.6vw;
    line-height: 1vw;
}

.Text p:nth-child(1){
    margin: 0;
}

.span{
    font-weight: 600;
}

.Space{
    line-height: 3vw;
}

.Logo img{
    width: 14.3vw;
    box-shadow: 0px 1.3vw 2.5vw rgba(0, 0, 0, 0.03);
}

@media screen and (max-width: 980px){
    .Menu{
        transform: scale(2);
        cursor: pointer;
    }
    .Text{
        display: none;
    }
    .TopInfo{
        width: 90%;
        margin-left: auto;
        margin-right: auto;
    }
    .Logo{
        width: 30%;
    }
    .Logo img{
        width: 100%;
    }
}

@media screen and (min-width: 980px){
    .Menu{
        display: none;
    }
    .Messangers{
        display: none;
    }
}